import React from "react"
import { useNavigate } from "react-router-dom"
import { BriefReportSuccessIcon } from "@rp/react/common/assets"
import { Box, Button, Typography } from "@rp/react/web/mui/core"
import { WATCHLIST_ID_ROUTE } from "@rp/react/web/router"

type BriefReportNoNewsProps = {
  watchlistId: string | undefined
}

const BriefReportNoNews = ({ watchlistId }: BriefReportNoNewsProps) => {
  const navigate = useNavigate()

  return (
    <Box
      sx={{
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        height: "calc(100vh - 240px)",
        width: "750px",
        justifyContent: "center",
      }}
    >
      <BriefReportSuccessIcon height="166px" width="150px" />

      <Typography lineHeight="24px" fontSize="20px" fontWeight={700} padding="16px">
        You're up to date!
      </Typography>

      <Typography
        lineHeight="24px"
        fontSize="16px"
        color="text.secondary"
        textAlign="center"
        mb="16px"
      >
        Our research agent hasn't detected new noteworthy market developments on your monitored
        securities. Rest assured, we'll notify you when new information becomes available.
      </Typography>

      <Button
        variant="contained"
        disabled={!watchlistId}
        onClick={() => watchlistId && navigate(WATCHLIST_ID_ROUTE.replace(":id", watchlistId))}
        aria-label="View Watchlist"
      >
        View Watchlist
      </Button>
    </Box>
  )
}

export { BriefReportNoNews }
